.header{background-color:#ffffff;padding:0;height:45px;box-shadow:4px 4px 40px 0 rgba(0,0,0,0.05);position:relative;display:flex;justify-content:space-between;z-index:999}.header .func-menu{line-height:45px;float:right}.header .rightWarpper{display:flex;padding-right:16px}.header .button{width:45px;height:45px;line-height:45px;text-align:center;font-size:14px;cursor:pointer;float:left;transition:all 0.3s ease-out}.header .button:hover{color:#108ee9;background-color:rgba(16,142,233,0.15)}



